<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">사용자 관리</h2>
      <p class="content-sub-title">사용자의 정보를 관리하는 페이지</p>

      <!-- 검색 컨트롤 -->
      <div class="d-flex justify-content-end name-group mt-2">
        <label for="nameSearch" class="mr-2">이름 :</label>
        <input
          type="text"
          class="form-control w-auto"
          id="nameSearch"
          v-model="nameSearch"
          placeholder="이름 입력"
        />
        <button class="btn btn-primary ml-2" @click="search">조회</button>
      </div>

      <!-- 테이블 보기 -->
      <table class="table mt-3" id="dataTable">
        <thead>
          <tr>
            <th>이름</th>
            <th class="d-none d-md-table-cell">이메일</th>
            <th class="d-none d-md-table-cell">부서</th>
            <th>팀</th>
            <th>직책</th>
            <th>상태</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in paginatedData" :key="index" @click="showModal(item)">
            <td>{{ item.name }}</td>
            <td class="d-none d-md-table-cell">{{ item.email }}</td>
            <td class="d-none d-md-table-cell">{{ item.department }}</td>
            <td>{{ item.team }}</td>
            <td>{{ item.position }}</td>
            <td>{{ item.status }}</td>
          </tr>
        </tbody>
      </table>

      <!-- 페이징 컨트롤 -->
      <nav class="d-flex justify-content-center">
        <ul class="pagination">
          <li class="page-item" :class="{ disabled: currentPage === 1 }" @click.prevent="prevPage">
            <a class="page-link" href="#">&lt;</a>
          </li>
          <li class="page-item"><a class="page-link" href="#">{{ currentPage }}</a></li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }" @click.prevent="nextPage">
            <a class="page-link" href="#">&gt;</a>
          </li>
        </ul>
      </nav>

      <!-- 사용자 세부 정보 모달 창 -->
      <b-modal v-model="modalVisible" title="사용자 세부 정보" @ok="handleSaveClick" ok-title="저장" cancel-title="취소">
        <b-form @change="handleChange">
          <!-- 이름, 생년월일 한 줄 -->
          <b-form-group label="이름/생년월일" label-for="userDetails">
            <div id="userDetails" class="d-flex">
              <b-form-input v-model="selectedUser.name" :disabled="true" placeholder="이름" class="mr-2"></b-form-input>
              <b-form-input id="userBirthDate" type="date" v-model="selectedUser.birthDate" :disabled="true"></b-form-input>
            </div>
          </b-form-group>

          <!-- 고용형태, 입사일/퇴사일 -->
          <b-form-group label="고용형태/입사일/퇴사일" label-for="employmentDetails">
            <div id="employmentDetails" class="d-flex">
              <b-form-select v-model="selectedUser.employmentType" :options="['정규직', '프리랜서']" :disabled="!isAdmin" class="mr-2"></b-form-select>
              <b-form-select v-model="selectedUser.status" :options="['입사', '퇴사']" class="mr-2"></b-form-select>
              <b-form-input type="date" v-model="selectedUserDate" :disabled="true"></b-form-input>
            </div>
          </b-form-group>

          <!-- 부서, 팀, 직책 한 줄 -->
          <b-form-group label="부서/팀/직책" label-for="userEmploymentDetails">
            <div id="userEmploymentDetails" class="d-flex">
              <b-form-input v-model="selectedUser.department" :disabled="!isAdmin" placeholder="부서" class="mr-2"></b-form-input>
              <b-form-input v-model="selectedUser.team" :disabled="!isAdmin" placeholder="팀" class="mr-2"></b-form-input>
              <b-form-input v-model="selectedUser.position" :disabled="!isAdmin" placeholder="직책"></b-form-input>
            </div>
          </b-form-group>

          <!-- 우편번호 및 주소 -->
          <b-form-group label="우편번호/주소" label-for="userZipCode">
            <div id="userAddress" class="d-flex">
              <b-form-input v-model="selectedUser.zipCode" :disabled="true" placeholder="우편번호" class="mr-2" style="width: 33%"></b-form-input>
            </div>
            <b-form-input v-model="selectedUser.address" :disabled="true" placeholder="주소" style="width: 100%"></b-form-input>
          </b-form-group>

          <!-- 현재 파견지 -->
          <b-form-group label="현재 파견지" label-for="userLocation">
            <b-form-input id="userLocation" v-model="selectedUser.location" :disabled="!isAdmin" placeholder="현재 파견지"></b-form-input>
          </b-form-group>
        </b-form>
      </b-modal>

      <!-- 저장 확인용 모달 -->
      <b-modal v-model="confirmationModalVisible" title="저장 확인" ok-title="확인" cancel-title="취소" @ok="saveUserDetails" centered no-close-on-backdrop>
        <template #modal-footer>
          <b-button variant="secondary" @click="confirmationModalVisible = false">취소</b-button>
          <b-button variant="danger" @click="saveUserDetails">확인</b-button>
        </template>
        <p>변경 사항을 저장하시겠습니까?</p>
        <p v-if="statusChanged" style="color: red;">입사/퇴사 처리가 포함되어 있습니다.</p>
      </b-modal>

      <!-- 변경된 데이터가 없을 때 표시할 모달 -->
      <div class="modal fade show d-block" tabindex="-1" role="dialog" v-if="noChangesModalVisible" @click.self="closeNoChangesModal">
        <div class="modal-dialog modal-dialog-centered" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">알림</h5>
            </div>
            <div class="modal-body">
              <p>변경된 데이터가 없습니다.</p>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-primary" @click="closeNoChangesModal">확인</button>
            </div>
          </div>
        </div>
      </div>

      <!-- 저장 완료 모달 -->
      <div class="modal fade show d-block" tabindex="-1" role="dialog" v-if="saveCompleteModalVisible" @click.self="closeSaveCompleteModal">
        <div class="modal-dialog modal-dialog-centered" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">{{ saveCompleteMessage }}</h5>
            </div>
            <div class="modal-body">
              <p>{{ saveCompleteMessage }}</p>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-primary" @click="closeSaveCompleteModal">확인</button>
            </div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue';
import axios from 'axios';

// 관리자 여부
const isAdmin = ref(true);

// 모달 관련 상태
const modalVisible = ref(false);
const confirmationModalVisible = ref(false);
const saveCompleteModalVisible = ref(false);
const noChangesModalVisible = ref(false); // 변경된 데이터가 없을 때 사용할 모달
const saveCompleteMessage = ref('저장되었습니다.');  // 저장 완료 모달 메시지

const selectedUser = ref({
  name: '',
  email: '',
  department: '',
  team: '',
  position: '',
  joinDate: '',
  leaveDate: '',
  status: '입사',
  birthDate: '',
  employmentType: '정규직',
  zipCode: '',
  address: '',
  location: ''
});
const originalUser = ref({ ...selectedUser.value });

// 상태 변경 감지 변수
const statusChanged = ref(false);
const dataChanged = ref(false);

// 변경 사항 감지 함수
const handleChange = () => {
  dataChanged.value = JSON.stringify(originalUser.value) !== JSON.stringify(selectedUser.value);
  statusChanged.value = selectedUser.value.status !== originalUser.value.status;
};

// 저장 버튼을 클릭했을 때 호출되는 함수
const handleSaveClick = () => {
  if (!dataChanged.value) {
    noChangesModalVisible.value = true; // 변경된 데이터가 없을 때 모달 띄우기
  } else {
    showConfirmationModal(); // 데이터가 변경되었을 때 저장 확인 모달 띄우기
  }
};

// 모달 닫기 함수
const closeNoChangesModal = () => {
  noChangesModalVisible.value = false;
};

// 저장 확인 모달 열기 함수
const showConfirmationModal = () => {
  confirmationModalVisible.value = true; // 재확인 모달 열기
};

// 저장 완료 모달 닫기 함수
const closeSaveCompleteModal = () => {
  saveCompleteModalVisible.value = false;
};

// 저장 함수 (재확인 모달에서 확인 버튼 클릭 시 호출)
const saveUserDetails = () => {
  modalVisible.value = false; // 기존 모달 닫기
  confirmationModalVisible.value = false; // 저장 확인 모달 닫기
  
  // Axios를 이용해 백엔드에 저장 요청
  axios.post('/api/saveUser', selectedUser.value)
    .then(response => {
      console.log(response.data);
      // 성공 시 저장 완료 모달 띄우기
      saveCompleteMessage.value = '저장되었습니다.';
      saveCompleteModalVisible.value = true;
    })
    .catch(error => {
      console.log(error);
      // 실패 시 에러 메시지 출력
      saveCompleteMessage.value = '저장에 실패했습니다.(기능 미구현)';
      saveCompleteModalVisible.value = true;
    });
};

// 사용자 데이터 및 페이징 처리
const currentPage = ref(1);
const rowsPerPage = ref(5);
const nameSearch = ref('');
const data = ref([
  { name: '권범준', email: 'qjawns0618@kpcnc.co.kr', department: 'SI사업본부', team: '프레임워크', position: '책임', joinDate: '2022-01-01', leaveDate: '', status: '입사', birthDate: '1990-01-01', employmentType: '정규직', zipCode: '12345', address: '서울 강남구', location: '서울' },
  { name: '김철수', email: 'kim@example.com', department: 'AI사업본부', team: 'RPA', position: '책임', joinDate: '2021-07-15', leaveDate: '', status: '퇴사', BirthDate: '1985-06-15', employmentType: '프리랜서', zipCode: '67890', address: '부산 해운대구', location: '부산' },
  { name: '이영희', email: 'lee@example.com', department: '경영관리부', team: '영업', position: '선임', joinDate: '2023-03-12', leaveDate: '', status: '입사', BirthDate: '1995-03-12', employmentType: '정규직', zipCode: '54321', address: '인천 서구', location: '인천' },
]);

const filteredData = computed(() => {
  return data.value.filter(item => item.name.includes(nameSearch.value));
});

const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * rowsPerPage.value;
  return filteredData.value.slice(start, start + rowsPerPage.value);
});

const totalPages = computed(() => Math.ceil(filteredData.value.length / rowsPerPage.value));

// 사용자 검색
const search = () => {
  currentPage.value = 1;
};

// 페이지 변경
const prevPage = () => {
  if (currentPage.value > 1) currentPage.value--;
};
const nextPage = () => {
  if (currentPage.value < totalPages.value) currentPage.value++;
};

// 사용자 클릭 시 모달 열기
const showModal = (user) => {
  selectedUser.value = { ...user };
  originalUser.value = { ...user }; // 기존 값 저장
  statusChanged.value = false;
  dataChanged.value = false;
  modalVisible.value = true;
};

// 입사/퇴사 날짜 계산
const selectedUserDate = computed(() => {
  return selectedUser.value.status === '입사' ? selectedUser.value.joinDate : selectedUser.value.leaveDate;
});

</script>

<style scoped>
#dataTable tbody tr:hover td {
  background-color: #e7f1ff !important;
}

/* 오른쪽 정렬 */
.name-group {
  justify-content: flex-end;
}

.custom-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.custom-modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
}

/* 반응형: 이메일과 생년월일을 모바일에서 숨김 */
@media (max-width: 768px) {
  .d-md-table-cell {
    display: none;
  }
}

#userAddress {
  margin-bottom: 10px;
}
</style>
